.input-container{    
    width: 70%;    
}

.regular-style-dark{
    width: 100%;
    height: 35px;
    border-radius: 5px;
    border: none;
    padding: 0 100px 0 20px;
    box-sizing: border-box;
    margin-bottom: 10px;
    color: cadetblue;
    font-size: 14px;
    outline: none;
}

.regular-style-dark::placeholder{
    font-size: 14px;
    color: white;
    opacity: 0.8;
}

.input-error{
    border: 1px solid red;
    color: red;
    background: rgb(224, 204, 204);
    height: 35px;
    width: 100%;
    border-radius: 25px;
    padding: 0 100px 0 20px;
    box-sizing: border-box;
    margin-bottom: 10px;
    font-size: 14px;
    outline: none;
}

.input-error::placeholder{
    color: red;
    font-size: 14px;
}

